import React, { Component } from 'react'
import HeaderHelpInfo from './HeaderHelpInfo'

class HeaderHelp extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        show: false
    }
    toggle = () => this.setState((currentState) => ({show: !currentState.show}));
    render() {
        return (
            <div className={`help ${this.state.show ? "open" : "close"}`} onClick={this.toggle}>
                {this.state.show &&
                    <HeaderHelpInfo />
                }
            </div>
        );
    }
}

export default HeaderHelp;