import React from 'react'
import News from '../data/news.json'

const ContentNews = () => (
    <div className="newsEdito col-xl-9">
        <h2>Actualités</h2>
        <div className="whiteBlock">
            <ul className="newsList">
                {News.map((listNews, i) =>
                <li key={i} className={`newsItem ` + listNews.NewsStatut}>
                    {listNews.NewsImg ? 
                        <div className="newsBlock">
                            <div className="flex-items-center">
                                <img src={listNews.NewsImg} alt={listNews.NewsTitre} className="newsImg"/>
                                <div className="heading-4">{listNews.NewsTitre}</div>
                            </div>
                            <div className="newsDate">Publié le {listNews.NewsDate}</div>
                        </div> :
                        <div className="newsBlock">
                            <div className="heading-4">{listNews.NewsTitre}</div>
                            <div className="newsDate">Publié le {listNews.NewsDate}</div>
                        </div>
                    }
                </li>
                )}
            </ul>
        </div>
    </div>
);

export default ContentNews;