import React from 'react'
import ListUC from './ListUC'

const ListChap = (props) => {         
    return (
        <div>
            {props.chapList.map((list, i) =>
            <div key={i} className="row blockChapter">
                <div className="col-xl-offset-1 col-xl-8">
                    <h2 id={`chapter-`+(i+1)} className="flex-items-center">
                        <span className="nberChapter flex-items-center">{(i+1)}</span>
                        {list.chapTitle}
                    </h2>
                    <ListUC ucList={list.uc} docuRef={props.docuRef} partPath={props.partPath}/>
                </div>
            </div>
            )}
        </div>
    )
}

export default ListChap;