import React, {Component} from 'react'
import { NavLink } from 'react-router-dom'
import { HashLink as Link } from 'react-router-hash-link';
import Sommaire from '../data/sommaire_v2.json'
import SidebarPart from './SidebarPart'
import SidebarPartChap from './SidebarPartChap'
import ListDoc from './ListDoc'

class Sidebar extends Component {
    constructor(props) {
        super(props);
        this.Ref = this.sidebarDocRef;
    }
    state = {
        show: false
    }
    toggle = () => this.setState((currentState) => ({show: !currentState.show}));

    render() {
        let ListItems;
        this.props.sidebarDocSom.some(el => {
            if(el.hasOwnProperty('chapitres')) {
                ListItems = <SidebarPartChap {...this.props}/>
            } else {
                ListItems = <SidebarPart {...this.props}/>
            }
        })
        return(
            <div className="colSommaire">
                <div>
                    <div className={this.state.show ? "currentDoc whiteBlock active" : "currentDoc whiteBlock"} onClick={this.toggle}>
                        <div className="currentDocTitle">{Sommaire[this.props.sidebarDocRef].documentation}</div>
                        <div className="seeAllThem flex-items-center"></div>
                    </div>
                    {this.state.show && 
                        <div className="navThem" onClick={this.toggle}>
                            <ListDoc />
                        </div>
                    }
                </div>
                {ListItems}
            </div>
        );

    }
}

export default Sidebar;